import React, { Component } from "react";
import ReactDOM from "react-dom";
import ScrollArea from "react-scrollbar";
import "./search-results.css";
import ListGroup from "react-bootstrap/ListGroup";
import { guid } from "../../.././src/store/utility";
import Pagination from "react-bootstrap/Pagination";
import Spinner from "react-bootstrap/Spinner";
import { faArrowCircleLeft } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import TutorialVideo from "../../assests/search-tutorial-video-3.mp4";
import LocationWeather from "../location-weather-data/location-weather-data";

const PAGINATION_LIMIT = 10;
let resultList = [];
let renderedComponent = <div></div>;

const currentComponent = {
  DEFAULT: 1,
  LIST: 2,
  DETAILS: 3,
  LOADING: 4,
};
let resultCount = 0;
let loadedComponent = currentComponent.DEFAULT;

class SearchResultsList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentPage: 1,
      resultsPerPage: 10,
      loadQueryData: false,
      queryResultData: null,
      queryLocation: null,
      loading: false,
    };
  }

  componentDidMount() {
    resultList = [];
    this.setState({
      currentPage: 1,
    });

    let test = document.getElementById("vid");
    console.log(test);
    if(test){
      test.play();
    }
  }

  onClick(e, index) {
    let selectedLocation = resultList[index];
    this.setState({
      loadQueryData: true,
      loading: true,
      queryLocation: selectedLocation,
    });
    const { getLocationDetails, loadMapData } = this.props;
    const [locations, locationsDispatch] = window.store.locations;
    getLocationDetails(
      selectedLocation,
      true,
      true,
      false,
      false,
      locationsDispatch
    ).then((result) => {
      const [locationDetails, locationDetailsDispatch] = window.store.locations;
      const mapData = locationDetails.locationData;
      if (mapData && mapData.length > 0) {
        this.setState({
          loadQueryData: true,
          queryResultData: JSON.parse(mapData),
        });
        loadMapData(mapData);
      }
      this.setState({
        loading: false,
      });
    });
  }

  defaultRender() {
    loadedComponent = currentComponent.DEFAULT;
    return (
      <div>
        <video
          id="vid"
          src={TutorialVideo}
          type="video/mp4"
          preload="true"
          autoplay="autoplay"
          muted
          loop="loop"
          paused="false"
        ></video>
      </div>
      // <div>
      //   <p>
      //     Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do
      //     eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad
      //     minim veniam, quis nostrud exercitation ullamco laboris nisi ut
      //     aliquip ex ea commodo consequat. Duis aute irure dolor in
      //     reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla
      //     pariatur.
      //   </p>
      //   <p>
      //     Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do
      //     eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad
      //     minim veniam, quis nostrud exercitation ullamco laboris nisi ut
      //     aliquip ex ea commodo consequat. Duis aute irure dolor in
      //     reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla
      //     pariatur. Excepteur sint occaecat cupidatat non proident, sunt in
      //     culpa qui officia deserunt mollit anim id est laborum.
      //   </p>
      //   <p>
      //     Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do
      //     eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad
      //     minim veniam, quis nostrud exercitation ullamco laboris nisi ut
      //     aliquip ex ea commodo consequat. Duis aute irure dolor in
      //     reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla
      //     pariatur.
      //   </p>
      //   <p></p>
      // </div>
    );
  }

  renderLoading() {
    loadedComponent = currentComponent.LOADING;
    return (
      <div className="spinner-container">
        <div className="spinner-box">
          <Spinner animation="border" role="status">
            <span className="sr-only">Loading...</span>
          </Spinner>
        </div>
      </div>
    );
  }

  changePage(event, pageNumber) {
    this.setState({
      currentPage: pageNumber,
    });
  }

  renderResultDisplay(searchResults) {
    loadedComponent = currentComponent.LIST;
    const resultListComponent = this.renderResultList(searchResults);
    return <div>{resultListComponent}</div>;
  }

  renderPagination(searchResults) {
    let pageCount = 0;
    const { currentPage, resultsPerPage } = this.state;
    let pageNumbersComponent = <div></div>;

    if (searchResults.length > PAGINATION_LIMIT) {
      pageCount = Math.ceil(searchResults.length / PAGINATION_LIMIT);

      const indexOfLastResult = currentPage * resultsPerPage;
      const indexOfFirstResult = indexOfLastResult - resultsPerPage;

      if (currentPage <= pageCount && currentPage !== 0) {
        resultList = [
          ...searchResults.slice(indexOfFirstResult, indexOfLastResult),
        ];
      }
      let items = [];
      for (let i = 1; i <= pageCount; i++) {
        items.push(
          <Pagination.Item
            key={i}
            active={i === currentPage}
            onClick={(event) => this.changePage(event, i)}
          >
            {i}
          </Pagination.Item>
        );
      }

      pageNumbersComponent = <Pagination size="sm">{items}</Pagination>;
    } else {
      resultList = [...searchResults];
    }

    return (
      <div className="row pagination-component">
        <ul id="page-numbers">{pageNumbersComponent}</ul>
      </div>
    );
  }

  renderResultList(searchResults) {
    let pagination = this.renderPagination(searchResults);
    loadedComponent = currentComponent.LIST;
    return (
      <div className="result-list">
        <ListGroup variant="flush">
          {resultList.map((searchResult, index) => {
            return (
              <ListGroup.Item
                action
                key={guid()}
                href={`${"#/search?location="}${
                  searchResult.location.locationName +
                  "+ " +
                  searchResult.region1
                }`}
                onClick={(event) => this.onClick(event, index)}
              >
                <div className="location-container">
                  <div className="location-name">
                    {searchResult.location.locationName}
                  </div>
                  <div className="location-admins">
                    {"County: "}
                    {searchResult.region1}, {" Ward: "}
                    {searchResult.region3}
                  </div>
                </div>
              </ListGroup.Item>
            );
          })}
        </ListGroup>
        <div className="pagination-container">{pagination}</div>
      </div>
    );
  }

  renderResultData(locData, soilsData, currentData, forecastData) {
    loadedComponent = currentComponent.DETAILS;
    return (
      <LocationWeather
        isLoading={false}
        locationData={locData}
        soilsData={soilsData}
        currentData={currentData}
        forecastData={forecastData}
      />
    );
  }

  refreshApp(event) {
    window.location.reload(false);
  }

  reSet(event) {
    const { searchResults, startTime, endTime } = this.props;
    const { loadQueryData, queryLocation, queryResultData } = this.state;
    this.setState({
      currentPage: 1,
      loading: false,
      loadQueryData: false,
      queryResultData: null,
      queryLocation: null,
    });
    if (loadedComponent === currentComponent.DETAILS) {
      const { searchResults } = this.props;
      if (searchResults && searchResults.length > 0) {
        let results = JSON.parse(searchResults);
        resultCount = results.length;
        renderedComponent = this.renderResultDisplay(results);
      } else {
        renderedComponent = this.defaultRender();
      }
    }
  }

  summaryStatement(resultCount, timeStatement) {
    if (resultCount > 0) {
      return (
        <div>
          <FontAwesomeIcon icon={faArrowCircleLeft} size="lg" />
          {[resultCount, " matching locations found", timeStatement]}
        </div>
      );
    } else {
      return "";
    }
  }

  render() {
    resultList = [];
    let makeCountZero = false;
    const { searchResults, startTime, endTime, showLoading } = this.props;
    const {
      loadQueryData,
      queryLocation,
      queryResultData,
      loading,
    } = this.state;

    const timeElapsed = endTime - startTime;
    const timeStatement =
      timeElapsed >= 1000
        ? " in " + (timeElapsed / 1000).toString() + "s"
        : timeElapsed > 0
        ? " in " + timeElapsed.toString() + "ms"
        : "";
    if (loadedComponent !== currentComponent.LOADING && !showLoading) {
      renderedComponent = this.defaultRender();
    }

    resultCount = 0;

    if (loading && loadedComponent !== currentComponent.LOADING) {
      renderedComponent = this.renderLoading();
    }

    if (showLoading) {
      renderedComponent = this.renderLoading();
    }

    if (!loading && !showLoading && searchResults && searchResults.length > 0) {
      let results = JSON.parse(searchResults);
      resultCount = results.length;
      if (resultCount === 1 && results && results[0].location.id === 0) {
        loadedComponent = currentComponent.DETAILS;
        makeCountZero = true;
        renderedComponent = (
          <div>
            <a href="#" onClick={(event) => this.refreshApp(event)}>
              <h5>Something went wrong. Location not found</h5>
            </a>
          </div>
        );
      } else if (!showLoading && !loading && searchResults && queryResultData) {
        if (queryResultData) {
          loadedComponent = currentComponent.DETAILS;
          const soilsData =
            queryResultData.mapData.features[0].properties.soils[0];
          const locData = {
            location: queryResultData.location.locationName,
            region1: queryResultData.region1,
            region2: queryResultData.region2,
            region3: queryResultData.region3,
            region4: queryResultData.region4,
            region5: queryResultData.region5,
          };

          renderedComponent = (
            <LocationWeather
              isLoading={false}
              locationData={locData}
              soilsData={soilsData}
              currentData={queryResultData.currentData.forecasts[0]}
              forecastData={queryResultData.forecasts.forecasts.map((obj) => {
                return obj.forecast[0];
              })}
            />
          );
        } else {
          renderedComponent = this.renderResultDisplay(results);
        }
      } else {
        renderedComponent = this.renderResultDisplay(results);
      }
    }

    let forecastsData = null;
    let hourlyData = null;
    if (queryResultData) {
      forecastsData = queryResultData.forecasts.forecasts;
      hourlyData = queryResultData.currentData.forecasts[0];
    }

    if (
      !loading &&
      !showLoading &&
      loadQueryData &&
      queryResultData &&
      forecastsData &&
      hourlyData &&
      forecastsData.length > 0 &&
      hourlyData.forecast.length > 0
    ) {
      loadedComponent = currentComponent.DETAILS;
      const soilsData = queryResultData.mapData.features[0].properties.soils[0];
      const locData = {
        location: queryResultData.location.locationName,
        region1: queryResultData.region1,
        region2: queryResultData.region2,
        region3: queryResultData.region3,
        region4: queryResultData.region4,
        region5: queryResultData.region5,
      };
      renderedComponent = (
        <LocationWeather
          locationData={locData}
          soilsData={soilsData}
          currentData={hourlyData.forecast}
          forecastData={forecastsData.map((obj) => {
            return obj.forecast[0];
          })}
        />
      );
    } else if (
      loading &&
      !showLoading &&
      loadQueryData &&
      queryResultData &&
      forecastsData &&
      hourlyData &&
      forecastsData.length === 0 &&
      hourlyData.forecast.length === 0
    ) {
      loadedComponent = currentComponent.DETAILS;
      renderedComponent = (
        <div>
          <p>NO RESULTS FOUND. PLEASE TRY AGAIN</p>
        </div>
      );
    } else if (
      loading &&
      !showLoading &&
      loadQueryData &&
      forecastsData === null &&
      hourlyData === null &&
      queryResultData === null &&
      loadedComponent !== currentComponent.LOADING
    ) {
      loadedComponent = currentComponent.DETAILS;
      renderedComponent = (
        <div>
          <a href="#" onClick={(event) => this.reSet(event)}>
            <h5>Something went wrong</h5>
          </a>
        </div>
      );
    }

    if (
      searchResults &&
      !showLoading &&
      loadQueryData &&
      queryResultData &&
      !loading
    ) {
      loadedComponent = currentComponent.DETAILS;
      const soilsData = queryResultData.mapData.features[0].properties.soils[0];
      const locData = {
        location: queryResultData.location.locationName,
        region1: queryResultData.region1,
        region2: queryResultData.region2,
        region3: queryResultData.region3,
        region4: queryResultData.region4,
        region5: queryResultData.region5,
      };

      renderedComponent = (
        <LocationWeather
          isLoading={false}
          locationData={locData}
          soilsData={soilsData}
          currentData={queryResultData.currentData.forecasts[0]}
          forecastData={queryResultData.forecasts.forecasts.map((obj) => {
            return obj.forecast[0];
          })}
        />
      );
    }

    let renderResultSummary =
      resultCount === 0 && loadedComponent === currentComponent.DETAILS ? (
        <div className="result-summary">
          <a href="#" onClick={(event) => this.reSet(event)}>
            <h5>
              <FontAwesomeIcon icon={faArrowCircleLeft} size="lg" />
              Back to result list
            </h5>
          </a>
        </div>
      ) : (
        <div className="result-summary">
          {" "}
          <a href="#" onClick={(event) => this.reSet(event)}>
            <h5>
              {this.summaryStatement(
                makeCountZero ? 0 : resultCount,
                timeStatement
              )}
            </h5>
          </a>
        </div>
      );

    return (
      <div className="row result-display">
        {renderResultSummary}
        <React.Fragment>
          <ScrollArea smoothScrolling={true}>
            {() => <div>{renderedComponent}</div>}
          </ScrollArea>
        </React.Fragment>
      </div>
    );
  }
}

export default SearchResultsList;
