import React, { Component } from 'react';
import Map from '../components/map/Map';
import SearchSuggestions from "../components/search-suggestions/search-suggestions";
import SearchResultsList from "../components/search-results/search-results";
import "../styles/home-styles.css";

import { getLocations, getLocationResults, getLocationDetails } from '../store/epic/locationSearchEpic';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import ex from '../assests/img/ex.png';
import w3 from '../assests/img/w3.png';
import w4 from '../assests/img/w4.webp';
import w5 from '../assests/img/w5.jpg'
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap'
import Image from 'react-bootstrap/Image'
import FigureCaption from "react-bootstrap/FigureCaption";

let searchLocation = '';
let selectedLocation = null;

class Home extends Component {


    constructor(props) {
        super(props);
        this.onChange.bind(this);
        this.state = {
            value: "",
            locationMapData: null,
            suggestions: null,
            locationList: null,
            startTime: Date.now(),
            endTime: Date.now(),
            currentResultList: null,
            selectedPoint: {},
            showLoading: false,
            isPoint: false,
            showSuggestions: false
        };

    }

    onKeyDown = e => {
        if (e.keyCode === 13) {
            this.locationSelected(0);
        }
    }

    loadMapData(mapData) {
        if (mapData) {
            this.setState({
                locationMapData: mapData
            });
        } else {
            this.setState({
                locationMapData: null
            });
        }
    }

    pointSelected(point) {
        if (point) {
            this.setState({
                value: searchLocation,
                suggestions: null,
                locationList: null,
                currentResultList: null,
                selectedPoint: point,
                showLoading: true,
                startTime: Date.now()
            });

            let queryPoint = point.lat + "," + point.lng;
            const [locations, locationsDispatch] = window.store.locations;
            getLocationResults(queryPoint, '', false, locationsDispatch).then(result => {
                const [locationResult, locationResultDispatch] = window.store.locations;
                this.setState({
                    value: queryPoint,
                    isPoint: true,
                    showLoading: false,
                    locationList: locationResult.locationResults,
                    endTime: Date.now()
                });
            });
            this.setState({
                showLoading: false
            });
        }
    }

    locationSelected(index) {
        this.setState({
            showLoading: true
        });
        const { value, suggestions, isPoint } = this.state;
        let searchCoordinates = isPoint;
        const [locations, locationsDispatch] = window.store.locations;
        let searchSuggestions = [];
        if (suggestions && suggestions.length > 0) {
            searchSuggestions = JSON.parse(suggestions);
            const testCoordinates = value.trim().replace(/\s+/g, '').split(',');

            if (testCoordinates && testCoordinates.length === 2) {
                const regex = /^[-+]?\d*\.?\d*$/;

                if (regex.test(testCoordinates[0]) && regex.test(testCoordinates[1])) {
                    searchCoordinates = true;
                }
            }
            selectedLocation = searchSuggestions[index];
            searchLocation = selectedLocation.location.locationName + ', ' + selectedLocation.region1;

            this.setState({
                value: searchLocation,
                suggestions: null,
                locationList: null,
                currentResultList: null
            });

            if (!searchCoordinates) {
                getLocationResults(selectedLocation.location.locationName, selectedLocation.region1, false, locationsDispatch).then(result => {
                    const [locationResult, locationResultDispatch] = window.store.locations;
                    this.setState({
                        value: searchLocation,
                        locationList: locationResult.locationResults,
                        showLoading: false
                    });
                });
            } else {
                getLocationResults(value, '', false, locationsDispatch).then(result => {
                    const [locationResult, locationResultDispatch] = window.store.locations;
                    this.setState({
                        value: value,
                        isPoint: false,
                        locationList: locationResult.locationResults,
                        showLoading: false
                    });
                });
            }
        }
    }

    onChange(e) {
        if (e.target != null && e.target.value !== '' && e.target.value.length > 0 && e.target.value !== "") {
            let searchValue = e.target.value;
            this.setState({
                startTime: Date.now(),
                showLoading: true
            });
            const [locations, locationsDispatch] = window.store.locations;
            getLocations(searchValue, '', false, locationsDispatch).then(result => {
                const [locationsSearch, locationsSearchDispatch] = window.store.locations;
                this.setState({
                    value: searchValue,
                    suggestions: locationsSearch.locations,
                    showSuggestions: true,
                    showLoading: false,
                    endTime: Date.now()
                });
            });
        } else {
            this.setState({
                value: '',
                suggestions: '',
                showLoading: false,
                showSuggestions: false
            })
        }
    }

    render() {
        const { value, suggestions, locationList, startTime, endTime, locationMapData, selectedPoint, showLoading, showSuggestions } = this.state;
        let searchSuggestions = [];

        if (suggestions && suggestions.length > 0) {
            searchSuggestions = JSON.parse(suggestions);
        }
        const orgLink = {
            marginRight: "8%",
        }
        const btnStyle = {
            margin: "25px",
            height: "100px",
            width: "120px",

        }
        const cls_md = {
            marginTop: "2em",
        }
        const floating_row = {
            position: "absolute",
            zIndex: "1000",
            width: "100%"
        }
        const fixed_row = {
            position: "relative"

        }
        const cls_map = {
            marginRight: "50px"
        }
        const imgc = {
            backgroundColor:"white",
            padding: "10px",
            textAlign: "center",
            fontFamily: "verdana",
            fontSize: "18px",
        }
        return (
            <div className="home-main" style={{backgroundColor:"#f5f5f5"}}>
                <Container>
                    <Row xs={1} md={1} sm={1} lg={2} xl={1}>
                        <Col sm={12} xs={12} lg={6} xl={6}>
                            <Row>
                                <Col sm={12}>
                                    <input type="search" name="search" placeholder="Search Location" className="form-control"
                                        onChange={(event) => this.onChange(event)}
                                        onKeyDown={(event) => this.onKeyDown(event)} />
                                </Col>
                            </Row>
                            <Row style={floating_row}>
                                <Col sm={12}>
                                    <SearchSuggestions
                                        showSuggestions={showSuggestions}
                                        selectLocation={(index) => this.locationSelected(index)}
                                        suggestions={searchSuggestions.length > 10 ? searchSuggestions.slice(0, 10) : searchSuggestions}
                                    />
                                </Col>
                            </Row>
                            <Row style={fixed_row}>
                                <Col sm={12}>
                                    <Map
                                        pointSelected={(point) => this.pointSelected(point)}
                                        resultDetails={locationMapData}
                                    />
                                </Col>
                            </Row>
                        </Col>
                        <Col sm={12} xs={12} lg={6} xl={6} >
                            <Row style={fixed_row}>
                                <Col sm={12}>
                                    <SearchResultsList
                                        showLoading={showLoading}
                                        endTime={endTime}
                                        startTime={startTime}
                                        loadMapData={(mapData) => this.loadMapData(mapData)}
                                        getLocationDetails={getLocationDetails}
                                        selectedLocation={selectedLocation}
                                        searchResults={locationList}
                                    />
                                </Col>
                            </Row>
                        </Col>
                    </Row>

                    { <Row>
                        <Col sm={12}>

                        </Col>
                    </Row> }
                    { <Row>
                        <Col sm={12}>

                        </Col>
                    </Row> }

                    { <Row>

                    </Row> }
                </Container>

                        <Container>
                            <div className="focus" style={{backgroundColor:"white", padding:"20px", textAlign:"center"}}>
                                <h3 >Agro-Weather </h3>
                            </div>
                            <div className="focus" style={{ paddingTop:"5px", paddingBottom:"20px", opacity:"1", fontFamily:"verdana"}}>
                                <p style={{fontSize:"16px", }}>Weather Impact provides agribusinesses with easy to access, reliable weather and
                                    climate information. Our mission is to support farmers to optimize global food
                                    production and reduce the impact of climate change.</p>

                                <p>    Our services are flexible, tailor-made to the context, and available in various
                                    languages across Africa. We condense complex, real-time data into accurate,
                                    user-friendly texts, or a mobile app. Right now, with the help of our clients 
                                    digital platforms and other institutions that support farmers  over 190,000 farmers
                                    in East Africa receive weather forecasts directly on their mobile phones.</p>

                                 <p>   With our state-of-the-art weather models, our clients call on us for long term
                                    weather and climate-risk analyses. With this data, they support farmers in making
                                    decisions on when to plant or when to harvest. Traditionally, these decisions were
                                    based on predictable weather patterns but faced with increasingly erratic weather
                                    farmers suffer tremendous losses. Reliable local weather data prevents this loss and
                                     helps optimize global food production.</p>

                                   <p> In the past five years, we have partnered with various local organizations and
                                    National Meteorological Agencies in Africa to reach farmers in the last mile. We
                                    also work with a vast network of strategic partners. They utilize our data to
                                    develop accurate agricultural advice and enhance our product with their platforms
                                    and research.</p>
                            </div>

                        </Container>
                <Container>
                    { <Row>
                        <Col sm={12} style={imgc}>
                            <h3>Our services</h3>
                        </Col>

                    </Row> }

                    { <Row>
                        <Col sm={6} style={imgc}>
                            <Image src={ex} fluid={true}/>
                            <FigureCaption>Agro-Meteorological Forecasts</FigureCaption>
                        </Col>

                        <Col sm={6} style={imgc}>
                            <Image src={w3} fluid={true}/>
                            <FigureCaption>Extreme Weather Alerts</FigureCaption>
                        </Col>
                    </Row> }

                    { <Row>
                        <Col sm={6} style={imgc}>
                            <Image src={w4} fluid={true}/>
                            <FigureCaption>Farm-Management Advice</FigureCaption>
                        </Col>

                        <Col sm={6} style={imgc}>
                            <Image src={w5} fluid={true}/>
                            <FigureCaption>Climate Analytics</FigureCaption>
                        </Col>

                    </Row> }

                </Container>
                <Container>
                <div className="org-link">
                    <a href="#" style={orgLink}>MIS</a>
                    <a href="#" style={orgLink}>KARLO</a>
                    <a href="#" style={orgLink}>KCSAP</a>
                    <a href="#" style={orgLink}>KMD</a>
                    <a href="#" style={orgLink}>FB</a>
                    <a href="#" style={orgLink}>Twitter</a>
                    <a href="#" style={orgLink}>Call Center</a>
                    <a href="#" style={orgLink}>Details</a>
                </div>
                </Container>
            </div>
        );
    }
}


export default Home;