import React, { Component } from "react";
import "./location-weather-data.css";
import Spinner from "react-bootstrap/Spinner";
import WeatherDataContainer from "./weather-container/weather-container";


let weatherDetails = (
    <div></div>
)
class LocationWeatherData extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    };
  }

  renderWeatherData(soilsData, currentData, forecastData, locationData) {
    return (
      <div className="weather-data">
        <WeatherDataContainer
          locationData={locationData}
          soilsData={soilsData}
          currentData={currentData}
          forecastData={forecastData}
        />
      </div>
    );
  }

  renderLoading() {
    const{isloading} =this.props;
    this.setState({
        loading: isloading
      });
    return (
      <div className="spinner-container">
        <div className="spinner-box">
          <Spinner animation="border" role="status">
            <span className="sr-only">Loading...</span>
          </Spinner>
        </div>
      </div>
    );
  }

  stopLoading(){
      this.setState({
          loading : false
      })
  }

  render() {
    const { soilsData, currentData, forecastData, locationData, isLoading } = this.props;
    const { loading } = this.state;

    if(isLoading){
        weatherDetails = this.renderLoading();
    }
    if (soilsData && currentData && forecastData) {
      weatherDetails = this.renderWeatherData(
        soilsData,
        currentData,
        forecastData,
        locationData
      );
    }

    return <div className="location-weather">{weatherDetails}</div>;
  }
}

export default LocationWeatherData;
