import axios from 'axios';
import {GET_FAQS, GET_QUESTIONS, QUESTIONS_FAIL} from "./actionTypes";
import { returnErrors } from "./messages";
import { tokenConfig} from "../actions/auth";

export const getFaqs = () => (dispatch, getState) =>{
    axios.get('http://127.0.0.1:8000/faqs/', tokenConfig(getState))
        .then(res =>{
            dispatch({
                type: GET_FAQS,
                payload: res.data,
            });
            console.log(res.data)
        }).catch(err => dispatch(
            returnErrors(err.response.data, err.response.status)
    ));
};



export const getQuestions = (value) =>(dispatch) => {
    //Headers
    const config = {
        headers: {
            'Content-Type': 'application/json'
        }
    }
    //Request Body

    let filter = {
       where: {
         topic: value
       }
    };
    const body = JSON.stringify({filter});
    console.log(body)

    axios.get('/faqs?filter='+ body, config)
        .then(res=>{
            dispatch({
                type: GET_QUESTIONS,
                payload: res.data
            });
        }).catch(err=>{
            dispatch(returnErrors(err.response.data, err.response.status));
            dispatch({
                type: QUESTIONS_FAIL
            });
    })
}




export const tokenConfigs = getState =>{
    //Get token from state
    //const token = getState().auth.token;
    const token = getState().token;

    //Headers
    const config = {
        headers: {
            'Content-Type': 'application/json'
        }
    }

    // If token, add headers config
    if(token){
        config.headers['Authorization'] = `Token ${token}`
    }

    return config;
}