import React, {Component} from 'react';
import { connect } from 'react-redux';
import * as PropTypes from 'prop-types';
import {addFarmer} from "../store/actions/farmer";
import {Options as boolean} from "yargs-parser";


class Farmer extends Component {
     constructor(props) {
    super(props);
    this.state = {
      boxAll: false,
        maize: false,
        beans: false,
        sorghum: false,
        dairy: false,
        chicken: false,
        fish: false,
        bee: false,
        red_meat: false

    };
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(e) {
    const { value, name, type, checked } = e.target;

    type === "checkbox"
      ? this.setState({ [name]: checked })
      : this.setState({ [name]: value });

    console.log(name+ ' ' +value)
  }

    state = {
        first_name: '', last_name: '',  gender: '', birthday: '', phone: '',
        email: '', passport_id: '', county: '', sub_county: '', ward: '',
        farm_size: '', maize: '', beans: '', sorghum: '', dairy: '',
        chicken: '', fish: '', bee: '', red_meat: '',checked: boolean,


    }
    static propTypes = {
        addFarmer: PropTypes.func.isRequired
    }
    onChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        });
    }

    onSubmit = e =>{
        e.preventDefault();
        const { first_name, last_name,  gender, birthday, phone, email, passport_id, county, sub_county, ward,
        farm_size, maize, beans, sorghum, dairy, chicken, fish, bee, red_meat, } = this.state;

        const farmer = { first_name, last_name,  gender, birthday, phone, email, passport_id, county, sub_county, ward,
        farm_size, maize, beans, sorghum, dairy, chicken, fish, bee, red_meat};
        this.props.addFarmer(farmer);

        console.log(farmer);

        this.setState({
        first_name: '', last_name: '',  gender: '', birthday: '', phone: '',
        email: '', passport_id: '', county: '', sub_county: '', ward: '',
        farm_size: '', maize: '', beans: '', sorghum: '', dairy: '',
        chicken: '', fish: '', bee: '', red_meat: '',
        })


    }


    render() {

        const legend = {
            fontSize: "14px",
            fontWeight: "bold",
            marginBottom: "0px",
            width: "35%",
            border: "1px solid #ddd",
            borderRadius: "4px",
            padding: "5px 5px 5px 10px",
            backgroundColor: "#ffffff"
        }

        const fieldset = {
            border: "1px solid #ddd !important",
			margin: "10px",
			minWidth: "0",
			padding: "10px",
			position: "relative",
			borderRadius: "4px",
			backgroundColor: "#f5f5f5",
			paddingLeft: "10px!important"

        }


        const { first_name, last_name,  gender, birthday, phone, email, passport_id, county, sub_county, ward,
        farm_size, maize, beans, sorghum, dairy, chicken, fish, bee, red_meat } = this.state;

        return <div className='container'>

            <div className="container container-fluid">
                <div className="row">

                    <div className="row col-md-12">

                        <h3>Farmer Registration</h3>
                    </div>
                </div>
                <form onSubmit={this.onSubmit}>
                    <fieldset style={fieldset}>
                        <legend style={legend}>Biodata:</legend>

                        <div className="row col-md-12">


                            <div className="form-group col-md-3">
                                <label htmlFor="first_name">First name:</label>
                                <input
                                    type="text"
                                    id="first_name"
                                    name="first_name"
                                    className="form-control"
                                    onChange={this.onChange}
                                    value={first_name}
                                />
                            </div>

                            <div className="form-group col-md-3">
                                <label htmlFor="last_name">Last name:</label>
                                <input
                                    type="text"
                                    id="last_name"
                                    name="last_name"
                                    className="form-control"
                                    onChange={this.onChange}
                                    value={last_name}
                                />
                            </div>

                            <div className="form-group col-md-3">
                                <label htmlFor="gender">Gender:</label>
                                <select className="form-control" onChange={this.onChange} name='gender'>
                                    <option>Select</option>
                                    <option value="male">Male</option>
                                    <option  value="female">Female</option>
                                </select>
                            </div>

                            <div className="form-group col-md-3">
                                <label htmlFor="birthday">Birthday:</label>
                                <input type="date"
                                       id="birthday"
                                       name="birthday"
                                       className="form-control"
                                       onChange={this.onChange}
                                        value={birthday}
                                />
                            </div>

                        </div>

                        <div className="row col-md-12">

                            <div className="form-group col-md-4">
                                <label htmlFor="phone">Phone Number:</label>
                                <input type="text"
                                       id="phone"
                                       name="phone"
                                       className="form-control"
                                       onChange={this.onChange}
                                       value={phone}
                                />
                            </div>
                            <div className="form-group col-md-4">
                                <label htmlFor="email">Email:</label>
                                <input type="email"
                                       id="email"
                                       name="email"
                                       className="form-control"
                                       onChange={this.onChange}
                                       value={email}
                                />
                            </div>
                            <div className="form-group col-md-4">
                                <label htmlFor="passport_id">ID/Passport:</label>
                                <input type="text"
                                       id="passport_id"
                                       name="passport_id"
                                       className="form-control"
                                       onChange={this.onChange}
                                       value={passport_id}

                                />
                            </div>

                        </div>
                    </fieldset>


                    <fieldset style={fieldset}>
                        <legend style={legend}>Farm Details:</legend>
                        <div className="row col-md-12">

                            <div className="form-group col-md-3">
                                <label htmlFor="county">County:</label>
                                <select name="county" className="form-control" onChange={this.onChange}>
                                    <option>Select</option>
                                    <option value="kitui">Kitui</option>
                                    <option value="machakos">Machakos</option>
                                </select>
                            </div>

                            <div className="form-group col-md-3">
                                <label htmlFor="subcounty">Sub County:</label>
                                <select name="sub_county" className="form-control" onChange={this.onChange}>
                                    <option>Select</option>
                                    <option value="mutomo">Mutomo</option>
                                    <option value="kwavonza">Kwavonza</option>
                                </select>
                            </div>

                            <div className="form-group col-md-3">
                                <label htmlFor="ward">Ward:</label>
                                <select name="ward" className="form-control" onChange={this.onChange}>
                                    <option>Select</option>
                                    <option value="ward1">Ward 1</option>
                                    <option value="ward2">Ward 2</option>
                                </select>
                            </div>

                            <div className="form-group col-md-3">
                                <label htmlFor="farm_size">Farm Size in Arces:</label>
                                <select name="farm_size" className="form-control" onChange={this.onChange}>
                                    <option>Select</option>
                                    <option value="50">50</option>
                                    <option value="100">100</option>
                                </select>
                            </div>

                        </div>


                        <legend style={legend}>Crop Value Chain (KCSAP)</legend>
                        <br/>
                        <div className="row col-md-12">
                            Maize<input className="col-md-2"
                                        type="checkbox"
                                        name="maize"
                                        //onChange={this.onChange}
                                        onChange={this.handleChange}
                                        checked={this.maize}


                        />
                            Beans<input className="col-md-2"
                                        type="checkbox"
                                        name="beans"
                                        onChange={this.handleChange}
                                        checked={this.beans}
                        />
                            Sorghum<input className="col-md-2" type="checkbox"
                                          value={sorghum}
                                          name="sorghum"
                                          onChange={this.handleChange}
                                          checked={this.sorghum}
                        />
                        </div>

                        <legend style={legend}>Livestock Value Chains</legend>
                        <br/>
                        <div className="row col-md-12">
                            Dairy Cattle<input className="col-md-1"
                                               type="checkbox"
                                               name="dairy"
                                               onChange={this.handleChange}
                                               checked={this.dairy}
                        />
                            Indigenous Chicken<input className="col-md-1"
                                                     type="checkbox"
                                                     name="chicken"
                                                     onChange={this.handleChange}
                                                     checked={this.chicken}
                        />
                            Aquaculture(Fish)<input className="col-md-1"
                                                    type="checkbox"
                                                    name="fish"
                                                    onChange={this.handleChange}
                                                    checked={this.fish}

                        />
                            Apiculter(Bee)<input className="col-md-1"
                                                 type="checkbox"
                                                 name="bee"
                                                 onChange={this.handleChange}
                                                 checked={this.bee}
                        />
                            Red-Meat<input className="col-md-1"
                                           type="checkbox"
                                           name="red_meat"
                                           onChange={this.handleChange}
                                           checked={this.red_meat}
                        />
                        </div>
                        <div className="clear">&nbsp;</div>
                    <div>
                        <button type="submit" className="btn btn-primary">Submit</button>
                    </div>
                    </fieldset>

                </form>

            </div>

        </div>
    }
}


export default connect(null, {addFarmer})(Farmer);