import * as locationSearchService from '../../services/locationSearchService';
import {locationsReceived, locationResultsReceived, locationDataReceived} from '../actions/locationSearchCreator';

export function getLocations(locationName, admin01, getForecastData, dispatch) {
  return locationSearchService.getLocations((locationName? locationName: 'Nairobi'), admin01, getForecastData).then((result) => {
    dispatch(locationsReceived(result));
  });
}

export function getLocationResults(locationName, admin01, getForecastData, dispatch) {
  return locationSearchService.getLocationResults((locationName? locationName: 'Nairobi'), admin01, getForecastData).then((result) => {
    dispatch(locationResultsReceived(result));
  });
}

export function getLocationDetails(location, getForecastData, getAdmin03 = false, getAdmin04 = false, getAdmin05 = false, dispatch) {
  return locationSearchService.getLocationDetails(location, getForecastData, getAdmin03, getAdmin04, getAdmin05).then((result) => {
    dispatch(locationDataReceived(result));
  });
}
