import React from "react";
import { Route } from "react-router-dom";
import Hoc from "./hoc/hoc";

import Login from "./containers/Login";
import Signup from "./containers/Signup";
import Profile from "./containers/Profile";
import HomepageLayout from "./containers/Home";
import Homes from "./containers/Homes";
import Feedback from "./containers/Feedback";
import Farmer from "./containers/Farmer";
import Faq from "./containers/Faq";
import Researcher from "./containers/Researcher";
import Scientist from "./containers/Scientist";


const BaseRouter = () => (
  <Hoc>
    <Route path="/login" component={Login} />
    <Route path="/signup" component={Signup}/>
    <Route path="/profile/:id" component={Profile}/>
    <Route exact path='/home' component={Homes}/>
    <Route exact path='/farmer' component={Farmer}/>
    <Route exact path='/feedback' component={Feedback}/>
    <Route exact path='/faqs' component={Faq}/>
    <Route exact path='/scientist' component={Scientist}/>
    <Route exact path="/" component={Homes} />
  </Hoc>
);

export default BaseRouter;
