import axios from 'axios';
import {ADD_FARMER, GET_FARMER, DELETE_FARMER } from "./actionTypes";
import { createMessage, returnErrors } from "./messages";
import { tokenConfig} from "../actions/auth";

export const addFarmer = (farmer) => (dispatch, getState) =>{
    axios.post('http://127.0.0.1:8000/farmer/', farmer, tokenConfig(getState))
        .then(res =>{
            dispatch(createMessage({addFarmer: 'Farmer Added'}));
            dispatch({
                type: ADD_FARMER,
                payload: res.data
            });
        }).catch(err => dispatch(
            returnErrors(err.response.data, err.response.status)
    ));
};



export const getFarmer = () => (dispatch, getState) =>{
    axios.get('http://127.0.0.1:8000/farmer/', tokenConfigs(getState))
        .then(res =>{
            dispatch({
                type: GET_FARMER,
                payload: res.data
            });
        }).catch(err => dispatch(
            returnErrors(err.response.data, err.response.status)
    ));
};

export const deleteFarmer = id => (dispatch, getState) =>{
    axios.delete(`http://127.0.0.1:8000/farmer/${id}/`, tokenConfig(getState))
        .then(res =>{
            dispatch(createMessage({deleteFarmer: 'Farmer Deleted'}));
            dispatch({
                type: DELETE_FARMER,
                payload: id
            });

        }).catch(err => console.log(err));
};


export const tokenConfigs = getState =>{
    //Get token from state
    //const token = getState().auth.token;
    const token = getState().token;

    //Headers
    const config = {
        headers: {
            'Content-Type': 'application/json'
        }
    }

    // If token, add headers config
    if(token){
        config.headers['Authorization'] = `Token ${token}`
    }

    return config;
}