import {get, post} from './fetchService';

//const URL = 'http://localhost:5000/api/search';
const URL = 'http://178.62.54.12/api/search';

export function getLocations(query, admin01 = '', getForecastData = false) {
  return get(URL, {query, admin01, getForecastData});
}

export function getLocationResults(query, admin01 = '', getForecastData = false) {
  return get(URL, {query, admin01, getForecastData});
}

export function getLocationDetails(location, getForecastData = false, getAdmin03 = false, getAdmin04 = false, getAdmin05 = false){
  return post(URL, {getForecastData, getAdmin03, getAdmin04, getAdmin05}, JSON.stringify(location));
}