import axios from "axios";
import * as actionTypes from "./actionTypes";
import {AUTH_SUCCESS} from "./actionTypes";

export const authStart = () => {
  return {
    type: actionTypes.AUTH_START
  };
};

export const authSuccess = user => {
  return {
    type: actionTypes.AUTH_SUCCESS,
    user
  };
};

export const authFail = error => {
  return {
    type: actionTypes.AUTH_FAIL,
    error: error
  };
};

export const logout = () => {
  localStorage.removeItem("user");

  return {
    type: actionTypes.AUTH_LOGOUT
  };
};

export const checkAuthTimeout = expirationTime => {
  return dispatch => {
    setTimeout(() => {
      dispatch(logout());
    }, expirationTime * 1000);
  };
};

export const authLogin = (username, password) => {
  return dispatch => {
    dispatch(authStart());
    axios
      .post("http://127.0.0.1:8000/rest-auth/login/", {
        username: username,
        password: password
      })
      .then(res => {
        const user ={
          token: res.data.key,
          username,
          userId: res.data.user,
          is_farmer: res.data.user_type.is_farmer,
          is_general: res.data.user_type.is_general,
          is_scientist: res.data.user_type.is_scientist,
          is_government: res.data.user_type.is_government,
          is_admin: res.data.user_type.is_admin,
          is_supplier: res.data.user_type.is_supplier,
          expirationDate: new Date(new Date().getTime() + 3600 * 1000),
        }
        localStorage.setItem("User", JSON.stringify(user));
        dispatch(authSuccess(user));
        dispatch(checkAuthTimeout(3600));
        dispatch({
            type: AUTH_SUCCESS,
        })
      })
      .catch(err => {
        dispatch(authFail(err));
      });
  };
};

export const authSignup = (username, email, password1, password2) => {
    return dispatch => {
    dispatch(authStart());
    const user  ={
      username,
      email,
      password1,
      password2,
    }
    axios
      .post("http://127.0.0.1:8000/rest-auth/registration/", user )
      .then(res => {
        const user ={
          token: res.data.key,
          username,
          email,
          userId: res.data.user,
          expirationDate: new Date(new Date().getTime() + 3600 * 1000),
        }
        localStorage.setItem("User", JSON.stringify(user));
        dispatch(authSuccess(user));
        dispatch(checkAuthTimeout(3600));
      })
      .catch(err => {
        console.log(err.response)
        dispatch(authFail(err));
      });
  };
};

export const authCheckState = () => {
  return dispatch => {
    const user = JSON.parse(localStorage.getItem('user'));
    const token = localStorage.getItem("token");
    if (user === undefined || user === null) {
      dispatch(logout());
    } else {
      const expirationDate = new Date(user.expirationDate);
      if (expirationDate <= new Date()) {
        dispatch(logout());
      } else {
        dispatch(authSuccess(user));
        dispatch(
          checkAuthTimeout(
            (expirationDate.getTime() - new Date().getTime()) / 1000
          )
        );
      }
    }
  };
};

export const tokenConfig = getState =>{
    //Get token from state
    const token = getState().auth.token;

    //Headers
    const config = {
        headers: {
            'Content-Type': 'application/json'
        }
    }

    // If token, add headers config
    if(token){
        config.headers['Authorization'] = `Token ${token}`
    }

    return config;
}
