import React, { Component } from "react";
import { Link } from "react-router-dom";
import ListGroup from "react-bootstrap/ListGroup";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMapMarker } from "@fortawesome/free-solid-svg-icons";
import {guid} from "../../.././src/store/utility";

class SearchSuggestions extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  onClick(e, index) {
    const { selectLocation } = this.props;
    selectLocation(index);
  }

  guidPart() {
    return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
  }

  render() {
    const { suggestions, showSuggestions } = this.props;

    if (suggestions && suggestions.length > 0 && showSuggestions) {
      return (
        <ListGroup>
          {suggestions.map((suggestion, index) => {
            return (
              <ListGroup.Item
                action
                key={guid()+index}
                href={`${"#/search?location="}${
                  suggestion.location.locationName + "+ " + suggestion.region1
                }`}
                onClick={(event) => this.onClick(event, index)}
              >
                <i className="fas fa-map-marker mr-3">
                  <FontAwesomeIcon icon={faMapMarker} />
                </i>
              {suggestion.location.locationName}{', '}{suggestion.region1}
              </ListGroup.Item>
            );
          })}
        </ListGroup>
      );
    }

    return <div></div>;
  }
}

export default SearchSuggestions;
