import React, {Component} from 'react';
import { connect } from 'react-redux';
import * as PropTypes from 'prop-types';
import {addFeeback} from "../store/actions/feedback";
import {authSignup} from "../store/actions/auth";

class Feedback extends Component {
state = {
        first_name: '', last_name: '',  county: '', sub_county: '', ward: '',  phone_number: '',  email: '',
        comments: '',

    }

    static propTypes = {
        addFeeback: PropTypes.func.isRequired
    }
    onChange = e =>
        this.setState({[e.target.name]: e.target.value}
    );


onSubmit = e =>{
        e.preventDefault();
        const { first_name, last_name,  county, sub_county, ward, phone_number, email, comments } = this.state;

        const feedback = { first_name, last_name,  county, sub_county, ward, phone_number, email, comments};
        this.props.addFeeback(feedback);
        console.log(feedback)

        this.setState({
        first_name: '', last_name: '',  county: '', sub_county: '', ward: '',  phone_number: '',  email: '',
        comments: '',
        })
    }

    render() {
    const { first_name, last_name,  county, sub_county, ward, phone_number, email, comments} = this.state;

        const legend = {
            fontSize: "14px",
            fontWeight: "bold",
            marginBottom: "0px",
            width: "35%",
            border: "1px solid #ddd",
            borderRadius: "4px",
            padding: "5px 5px 5px 10px",
            backgroundColor: "#ffffff"
        }

        const fieldset = {
            border: "1px solid #ddd !important",
			margin: "10px",
			minWidth: "0",
			padding: "10px",
			position: "relative",
			borderRadius: "4px",
			backgroundColor: "#f5f5f5",
			paddingLeft: "10px!important"

        }



        return <div className='container'>


            <div className="container container-fluid">
                <div className="row">

                    <div className="row col-md-12">
                        <h3>Feedback Form</h3>
                    </div>
                </div>

                <form onSubmit={this.onSubmit}>

                    <fieldset style={fieldset}>
                        <legend style={legend}>Biodata:</legend>
                        <div className="row col-md-12">

                            <div className="form-group col-md-2">
                                <label htmlFor="fname">First name:</label>
                                <input
                                type="text"
                                    id="first_name"
                                    name="first_name"
                                    className="form-control"
                                    onChange={this.onChange}
                                    value={first_name}
                                />

                            </div>

                            <div className="form-group col-md-2">
                                <label htmlFor="lname">Last name:</label>
                                <input type="text"
                                    id="last_name"
                                    name="last_name"
                                    className="form-control"
                                    onChange={this.onChange}
                                    value={last_name}
                                />
                            </div>

                            <div className="form-group col-md-2">
                                <label htmlFor="county">County:</label>
                                <select name="county" className="form-control" onChange={this.onChange}>
                                    <option>Select</option>
                                    <option value="kitui">Kitui</option>
                                    <option value="machakos">Machakos</option>
                                </select>

                            </div>

                            <div className="form-group">
                                <label htmlFor="subcounty">Sub County:</label>
                                <select name="sub_county" className="form-control" onChange={this.onChange}>
                                    <option>Select</option>
                                    <option value="mutomo">Mutomo</option>
                                    <option value="kwavonza">Kwavonza</option>
                                </select>
                            </div>

                            <div className="form-group col-md-2">
                                <label htmlFor="ward">Ward:</label>
                                <select name="ward" className="form-control" onChange={this.onChange}>
                                    <option>Select</option>
                                    <option value="ward1">Ward 1</option>
                                    <option value="ward2">Ward 2</option>
                                </select>
                            </div>

                        </div>

                        <div className="row col-md-12">

                            <div className="form-group col-md-3">
                                <label htmlFor="phone">Phone Number:</label>
                                <input type="text"
                                       id="phone"
                                       name="phone_number"
                                       className="form-control"
                                       onChange={this.onChange}
                                       value={phone_number}
                                />
                            </div>

                            <div className="form-group col-md-3">
                                <label htmlFor="email">Email:</label>
                                <input type="email"
                                       id="email"
                                       name="email"
                                       className="form-control"
                                       onChange={this.onChange}
                                       value={email}
                                />
                            </div>

                        </div>


                    </fieldset>
                    <fieldset style={fieldset}>
                        <legend style={legend}>Comments</legend>
                        <textarea
                            type="text"
                            className="form-control"
                            cols="85"
                            name="comments"
                            rows="6"
                            required
                            onChange={this.onChange}
                            value={comments}
                        />
                        <div className="clear">&nbsp;</div>
                    <div>
                        <button type="submit" className="btn btn-primary">Submit</button>
                    </div>
                    </fieldset>


                </form>

            </div>

        </div>
    }
}


export default connect(null,  {addFeeback})(Feedback);