        constructor(props) {
        super(props);
        this.onChange.bind(this);
        this.state = {
            value: "",
            locationMapData: null,
            suggestions: null,
            locationList: null,
            startTime: Date.now(),
            endTime: Date.now(),
            currentResultList: null,
            selectedPoint: {},
            showLoading: false,
            isPoint: false,
            showSuggestions: false
        };

    }

    onKeyDown = e => {
        if (e.keyCode === 13) {
            this.locationSelected(0);
        }
    }

    loadMapData(mapData) {
        if (mapData) {
            this.setState({
                locationMapData: mapData
            });
        } else {
            this.setState({
                locationMapData: null
            });
        }
    }

    pointSelected(point) {
        if (point) {
            this.setState({
                value: searchLocation,
                suggestions: null,
                locationList: null,
                currentResultList: null,
                selectedPoint: point,
                showLoading: true,
                startTime: Date.now()
            });

            let queryPoint = point.lat + "," + point.lng;
            const [locations, locationsDispatch] = window.store.locations;
            getLocationResults(queryPoint, '', false, locationsDispatch).then(result => {
                const [locationResult, locationResultDispatch] = window.store.locations;
                this.setState({
                    value: queryPoint,
                    isPoint: true,
                    showLoading: false,
                    locationList: locationResult.locationResults,
                    endTime: Date.now()
                });
            });
            this.setState({
                showLoading: false
            });
        }
    }

    locationSelected(index) {
        this.setState({
            showLoading: true
        });
        const { value, suggestions, isPoint } = this.state;
        let searchCoordinates = isPoint;
        const [locations, locationsDispatch] = window.store.locations;
        let searchSuggestions = [];
        if (suggestions && suggestions.length > 0) {
            searchSuggestions = JSON.parse(suggestions);
            const testCoordinates = value.trim().replace(/\s+/g, '').split(',');

            if (testCoordinates && testCoordinates.length === 2) {
                const regex = /^[-+]?\d*\.?\d*$/;

                if (regex.test(testCoordinates[0]) && regex.test(testCoordinates[1])) {
                    searchCoordinates = true;
                }
            }
            selectedLocation = searchSuggestions[index];
            searchLocation = selectedLocation.location.locationName + ', ' + selectedLocation.region1;

            this.setState({
                value: searchLocation,
                suggestions: null,
                locationList: null,
                currentResultList: null
            });

            if (!searchCoordinates) {
                getLocationResults(selectedLocation.location.locationName, selectedLocation.region1, false, locationsDispatch).then(result => {
                    const [locationResult, locationResultDispatch] = window.store.locations;
                    this.setState({
                        value: searchLocation,
                        locationList: locationResult.locationResults,
                        showLoading: false
                    });
                });
            } else {
                getLocationResults(value, '', false, locationsDispatch).then(result => {
                    const [locationResult, locationResultDispatch] = window.store.locations;
                    this.setState({
                        value: value,
                        isPoint: false,
                        locationList: locationResult.locationResults,
                        showLoading: false
                    });
                });
            }
        }
    }

    onChange(e) {
        if (e.target != null && e.target.value !== '' && e.target.value.length > 0 && e.target.value !== "") {
            let searchValue = e.target.value;
            this.setState({
                startTime: Date.now(),
                showLoading: true
            });
            const [locations, locationsDispatch] = window.store.locations;
            getLocations(searchValue, '', false, locationsDispatch).then(result => {
                const [locationsSearch, locationsSearchDispatch] = window.store.locations;
                this.setState({
                    value: searchValue,
                    suggestions: locationsSearch.locations,
                    showSuggestions: true,
                    showLoading: false,
                    endTime: Date.now()
                });
            });
        } else {
            this.setState({
                value: '',
                suggestions: '',
                showLoading: false,
                showSuggestions: false
            })
        }
    }

