import axios from 'axios';
import {ADD_FEEDBACK, GET_FEEDBACK, DELETE_FEEDBACK } from "./actionTypes";
import { createMessage, returnErrors } from "./messages";
import { tokenConfig} from "../actions/auth";

export const addFeeback = (feedback) => (dispatch, getState) =>{
    axios.post('http://127.0.0.1:8000/feedback/', feedback, tokenConfig(getState))
        .then(res =>{
            dispatch(createMessage({feedbackmsg: 'Feedback Added'}));
            dispatch({
                type: ADD_FEEDBACK,
                payload: res.data
            });
        }).catch(err => dispatch(
            dispatch(createMessage({deletefeedback: 'feedback was not added'})),
            returnErrors(err.response.data, err.response.status)

    ));
};



export const getFeedback = () => (dispatch, getState) =>{
    axios.get('http://127.0.0.1:8000/feedback/', tokenConfigs(getState))
        .then(res =>{
            dispatch({
                type: GET_FEEDBACK,
                payload: res.data
            });
        }).catch(err => dispatch(
            returnErrors(err.response.data, err.response.status)
    ));
};

export const deleteFeedback = id => (dispatch, getState) =>{
    axios.delete(`http://127.0.0.1:8000/feedback/${id}/`, tokenConfig(getState))
        .then(res =>{
            dispatch(createMessage({deleteFeedback: 'Feedback Deleted'}));
            dispatch({
                type: DELETE_FEEDBACK,
                payload: id
            });

        }).catch(err => console.log(err));
};


export const tokenConfigs = getState =>{
    //Get token from state
    //const token = getState().auth.token;
    const token = getState().token;

    //Headers
    const config = {
        headers: {
            'Content-Type': 'application/json'
        }
    }

    // If token, add headers config
    if(token){
        config.headers['Authorization'] = `Token ${token}`
    }

    return config;
}